@import '~assets/styles/h3Imports';

@mixin show-transition {
  transition: 0.1s transform ease-in;
}

@mixin hide-transition {
  transition: 1s transform ease-out, 0s height 1s, 0s width 1s, 0s padding 1s;
}

@mixin sr-only {
  height: 1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  top: rem(-64);
  width: 1px;
}

.skip-to-main {
  @include hide-transition;
  @include sr-only;

  background-color: var(--skip-main-content-background-color);
  border-radius: 24px;
  color: var(--skip-main-content-color);
  display: inline-block;
  font-size: rem(18);
  line-height: line-height(20, 18);
  margin: var(--skip-main-content-margin, rem(8));
  min-width: rem(242);
  text-align: center;
  text-decoration: none;
  width: auto;
  z-index: 1000;

  &:focus {
    @include show-transition;

    border: 2px solid var(--text-button-outline);
    height: rem(48);
    outline: none;
    padding: rem(14) rem(16);
    transform: translate(0, rem(64));
  }

  &:focus-visible {
    border: none;
    outline: 2px solid var(--text-button-outline);
  }

  &:focus:not(:focus-visible) {
    border: none;
    outline: none;
  }
}

@media #{$small-down} {
  .skip-to-main:focus {
    width: calc(100vw - 1rem);
  }
}
